export default {
    namespaced: true,
    state: {
        players: [{}, {}],
        characters: [{}, {}, {}],
        skillQueue: [],
        currentTeam: [],
        tempQueue: [],
        room: null,
        skillInFocus: null,
        payupCart: null,
    },
    getters: {
        GetMyData: function (state) {
            return (id) => {
                const p = state.players.filter(player => player.id === id)[0]
                if (p) {
                    return p
                }
                return {}
            }
        },
        getMyCharList: function (state) {
            return (id) => {
                let charList = []
                state.characters.forEach((char) => {
                    if (char.belongs[id]) charList.push(char)
                    else charList.push(false)
                })
                return charList
            }
        },
        getEnemyCharList: function (state) {
            return (id) => {
                let charList = []
                state.characters.forEach((char) => {
                    if (!char.belongs[id]) charList.push(char)
                    else charList.push(false)
                })
                return charList
            }
        },
        getBattleRoom: function (state) {
            return state.room
        },
        getTempQueue: function (state) {
            return state.tempQueue
        },
        getSkillQueueById: function (state) {
            return (id) => {
                return state.skillQueue.reduce((filtered, s) => {
                    let texts = []
                    for (const effect of s.effects) {
                        if (effect.targets.includes(id)) {
                            texts.push({
                                tip: determineEffectToolTip(effect),
                                duration: getDurationText(effect.duration) 
                            })
                        }
                    }
                    if (texts.length > 0) {
                        filtered.push({
                            skillpic: s.skillpic,
                            msgs: texts,
                        })
                    }
                    return filtered
                }, [])
            }
        },
        getTempQueueByCharacter: function (state) {
            return index => {
                return state.tempQueue.filter(s => {
                    return s.targets.includes(index)
                })
            }
        },
        getCharacterByIndex: function (state) {
            return (index) => {
                return state.characters[index]
            }
        },
        getSkillByCaster: function (state) {
            return (caster, skill) => {
                return state.characters[caster].skills[skill]
            }
        }
    },
    mutations: {
        setGameState: function (state, payload) {
            state.characters = payload.characters
            state.players = payload.players
            state.tempQueue = []
            state.skillQueue = payload.skillQueue
        },
        setPlayerEnergyPool: function (state, payload) {
            state.players[payload.playerIndex].energyPool = payload.energyPool
        },
        setRoom: function (state, room) {
            state.room = room
        },
        addToTeam(state, char) {
            if (state.currentTeam.length >= 3) {
                return
            }

            for (let i = 0; i < state.currentTeam.length; i++) {
                if (state.currentTeam[i]._id === char._id) return
            }
            state.currentTeam.push(char)
        },
        setTargetables(state, choices) {
            for (const i in state.characters) {
                if (choices.includes(Number(i))) state.characters[i].isTarget = true
                else state.characters[i].isTarget = false
            }
        },
        resetTargetables(state) {
            for (let char of state.characters) {
                char.isTarget = false
            }
        },
        setSkillInFocus(state, skill) {
            state.skillInFocus = skill
        },
        setTempQueue(state, payload) {
            state.tempQueue = payload.tempQueue;
            state.characters = payload.characters;
        },
        setPayupCart(state, payload) {
            state.players[payload.p].payupCart = payload.pay
        }
    }
}

// enum order Damage, Invulnerability, DamageReduction, CooldownIncreasal, CooldownReduction,


function determineEffectToolTip(effect) {
    switch(effect.type){
        case 0: return `This character will take ${effect.damage_value} damage`
        case 1: return `This character is invulnerable`
        case 2: return `This character's skills deal ${effect.reductionValue} less damage`
        case 3: return `If this character uses a skill their cooldown will be increased by ${effect.numTurns}`
        case 4: return `If this character uses a skill their cooldown will be reduced by ${effect.numTurns}`
    }
}

function getDurationText(duration){
    const d = duration/2;
    if(d>1)return `${Math.floor(d)} turns left`
    if(d==1) return `${d} turn left`
    return "Ends this turn"
}