<template>
  <main id='app'>
    <canvas class='canvas' ref='canvas'></canvas>

    <transition name='fade'>
      <router-view v-if="imagesReady" v-show='isLoaded' />
    </transition>
  </main>
</template>

<script>
import axios from "axios";
export default {
  data(){
    return {
      imagesReady: false
    }
  },
  computed: {
    isLoaded() {
      return this.$store.state.midori.isLoaded;
    },
    update(){
      return this.$store.state.midori.update;
    }
  },
  created() {
    axios
      .get("/character")
      .then(res => {
        this.$store.commit("Roster/setData", res.data);
      })
      .catch(err => {
        alert(err)
      });
    this.$store.commit("user/SET_USER", {
      _id: Math.random()
        .toString(36)
        .replace(/[^a-z]+/g, "")
        .substr(0, 10),
      username: Math.random()
        .toString(36)
        .replace(/[^a-z]+/g, "")
        .substr(0, 9),
      isLoggedIn: true,
      avatarId: Math.random()
        .toString(36)
        .replace(/[^a-z]+/g, "")
        .substr(0, 5)
    });
  },
  mounted() {
    this.$store.dispatch("midori/setCanvas", this.$refs.canvas)
      .then(r => {
        this.imagesReady = r
      })
  },
  watch:{
    update(n){
      if(n){
        if(this.$store.state.midori.index === 2){
          this.$store.commit('midori/setHomeBackground')
        } else {
          this.$store.commit('midori/setTurnBackground')
        }
      }
      this.$store.state.midori.update = false
    }
  },
  beforeDestroy(){
    this.$store.commit('midori/cleanUp')
  }
};
</script>

<style lang="scss">
@import "./style.scss";
</style>
